.appBaner {
    width: 100%;
    height: 100px;
    background-color: #232222;
    padding: 0 25px 0 45px;
    display: grid;
    grid-template-columns: 152px auto 133px;

    &__text {
        color: #FFF;
        font-size: 24px;
        font-weight: 700;
        line-height: normal;
        align-self: center;
        padding-left: 83px;
    }
}